.o-editor {

    .image {
        .media-preview-container {
            position: relative;
            width: 100%;
            aspect-ratio: 16/9;
            padding: 24px;
            box-sizing: border-box;

            &.empty {
                display: flex;
                align-items: center;
                justify-content: center;
                background: rgb(250,250,250);

                .diagonal1,
                .diagonal2 {
                    content: '';
                    position: absolute;
                    top: 5%;
                    left: 5%;
                    width: 90%;
                    height: 90%;
                    background: rgb(220,220,220);
                }
                .diagonal1 {
                    clip-path: polygon(0% 0%, 100% calc(100% - 1px), 100% 100%, 0% calc(0% + 1px));
                }
                .diagonal2 {
                    clip-path: polygon(0% calc(100% - 1px), 100% 0%, 100% calc(0% + 1px), 0% 100%);
                }
                .inner{
                    position: relative;
                    padding: 100px;
                    background: radial-gradient(circle, rgb(250,250,250) 60%, rgba(0,212,255,0) 100%);
                    color: #a0a0a0
                }
            }
        
            img {
                object-fit: contain;
                width: 100%;
                height: 100%;
            }
        
            .reset-button{
                display: none;
                position: absolute;
                top: 10px;
                right: 10px;
                
                .dashicon {
                    margin-inline-end: 0;
                }
            }

            &:hover > .reset-button{
                display: block;
            }
        }
    }
}